const m = require("mithril");
const User = {
  list: [],
  api: function () {
    return m
      .request({
        method: "GET",
        url: "https://rem-rest-api.herokuapp.com/api/users",
        withCredentials: true,
      })
      .then((data) => {
        User.list = data.data;
        // console.log(data.data, "l");
      });
  },
};

module.exports = User;
